.table-comparison {
  border-spacing: 0;
  border-collapse: separate;
  table-layout: fixed;
  text-align: center;
  width: 100%;
  margin-bottom: 25px;
  font-weight: 700;
}
@media screen and (min-width: 950px) {
  .table-comparison thead {
    color: #002ea6;
  }
  .table-comparison thead th:first-of-type {
    text-align: left;
  }
  .table-comparison thead th {
    padding: 15px 25px;
  }
  .table-comparison thead th.table-logo {
    text-align: center;
  }
  .table-comparison thead th.table-logo img {
    width: 155px;
    padding: 15px 5px;
  }
  .table-comparison thead th.table-logo img.logo-one {
    max-width: 145px;
  }
  .table-comparison tbody tr td:first-of-type {
    text-align: center;
    color: #002ea6;
    padding: 18px;
    line-height: 30px;
    font-weight: 600;
  }
  .table-comparison tbody tr td:not(:first-of-type) {
    color: #002ea6;
  }
  .table-comparison tbody tr:nth-of-type(odd) {
    background-color: rgba(23, 252, 196, 0.2);
  }
  .table-comparison strong,
  .table-comparison b {
    color: #002ea6;
  }
  .table-comparison tr {
    vertical-align: initial;
  }
  .table-comparison tr.four-columns-heading-row {
    vertical-align: middle;
  }
  .table-comparison tr.four-columns-heading-row th {
    text-align: center;
    max-width: 9.375rem;
  }
  .table-comparison tr.four-columns-heading-row th:first-child {
    text-align: left;
  }
  .table-comparison tr.four-columns-heading-row th img {
    display: block;
    margin: 0 auto;
  }
  .table-comparison img {
    vertical-align: middle;
  }
  .table-comparison th {
    padding: 0.625rem;
  }
  .table-comparison th:first-of-type {
    padding-left: 0;
    padding-right: 0;
  }
  .table-comparison td {
    padding: 0.625rem;
    vertical-align: middle;
  }
  .table-comparison td:first-of-type {
    padding-left: 0;
    padding-right: 0;
  }
  .table-comparison tbody {
    color: #002ea6;
    text-align: center;
    font-family: "Libre Franklin", sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 30px;
  }
}
@media screen and (max-width: 950px) {
  .table-comparison {
    border: 1px solid #ccc;
    border-collapse: collapse;
    margin: 0;
    padding: 20px 0;
    margin: 20px 0;
    width: 100%;
    table-layout: fixed;
  }
  .table-comparison caption {
    font-size: 1.5em;
    margin: 0.5em 0 0.75em;
  }
  .table-comparison tbody {
    background-color: transparent !important;
  }
  .table-comparison tr {
    border: transparent;
    padding: 0;
  }
  .table-comparison th,
  .table-comparison td {
    padding: 0.625em;
    text-align: center;
  }
  .table-comparison th {
    font-size: 0.85em;
    letter-spacing: 0.1em;
    text-transform: uppercase;
  }
}
@media screen and (max-width: 950px) and (max-width: 950px) {
  .table-comparison {
    border: 0;
  }
  .table-comparison caption {
    font-size: 1.3em;
  }
  .table-comparison thead {
    border: none;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
  }
  .table-comparison tr {
    border-bottom: 3px solid #ddd;
    display: block;
    margin-bottom: 10px;
  }
  .table-comparison td {
    display: block;
    font-size: 0.875rem;
    text-align: center;
    color: #002ea6;
    background-color: white;
  }
  .table-comparison td.first-row {
    font-size: 1rem;
    color: #002ea6;
    text-align: center;
    background-color: #17fcc4;
    width: 100%;
  }
  .table-comparison td.first-row::before {
    content: unset;
    display: none !important;
  }
  .table-comparison td:is([data-label])::before {
    content: "";
    background-image: var(--logo-img);
    float: left;
    font-weight: 700;
    text-transform: uppercase;
    width: 100%;
    height: 45px;
    background-size: 135px;
    background-position: center center;
    background-repeat: no-repeat;
    margin-bottom: 9px;
  }
  .table-comparison td:last-child {
    border-bottom: 0;
    color: #002ea6;
    background-color: #e6fdf7;
  }
}

@media screen and (min-width: 950px) {
  .accent-column {
    border-left: 2.22px solid #002ea6;
    border-right: 2.22px solid #002ea6;
  }
  .accent-column.first-accent-column {
    border: 0 !important;
    position: relative;
  }
  .accent-column.first-accent-column:before {
    content: "";
    display: block;
    width: 100%;
    height: 110%;
    position: absolute;
    bottom: 0;
    left: 0;
    border: 2.22px solid #002ea6;
    border-bottom: 0;
    border-top-right-radius: 35px;
    border-top-left-radius: 35px;
    margin: 0;
  }
  .accent-column.last-accent-column {
    border: 0 !important;
    position: relative;
  }
  .accent-column.last-accent-column:after {
    content: "";
    display: block;
    width: 100%;
    height: 125%;
    position: absolute;
    top: 0;
    left: 0;
    border: 2.22px solid #002ea6;
    border-top: 0;
    border-bottom-right-radius: 35px;
    border-bottom-left-radius: 35px;
    margin: 0;
  }
}
@media screen and (max-width: 950px) {
  .accent-column {
    border-left: none;
    border-right: none;
  }
  .accent-column.first-accent-column {
    border-top: none;
  }
  .accent-column.last-accent-column {
    border-bottom: none;
  }
}
.block--g {
  padding-top: 0;
  padding-bottom: 35px;
}
.block--g table {
  border: 0 transparent !important;
}
.block--g table th,
.block--g table td {
  border-bottom: 0 transparent;
}
.block--g table th:not(:last-of-type),
.block--g table td:not(:last-of-type) {
  border-right: 0 transparent !important;
  border-bottom: 0 transparent !important;
}
.block--g .table-comparison {
  width: unset;
  margin: unset;
}